}
.image-transition
{
    animation: moveRight 2s ease-in infinite;
}
@keyframes moveRight {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(10px);
    }
    100% {
        transform: translateX(20px); 
    }
}